:root {
  --nav-blue: #4cabe2;
  --nav-bg: #0c0c0c;
  --name-purple: #a161d4;
  --nav-bg-lighter: #3f3f3f;
}

a {
  text-decoration: none;
}

a:visited {
  color: var(--nav-blue);
}

#myName {
  color: var(--name-purple);
  font-size: 3vmin;
  margin-left: 5vw;
  margin-top: 2vh;
  margin-right: 25vw;
}

.navBar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  height: 6vh;
  position: fixed;
  top: 0;
  width: 100vw;
  text-align: left;
  background-color: var(--nav-bg);
}

.PAC li {
  margin-right: 5vw;
  margin-top: 2vh;
  font-size: 2.3vh;
}

.Socials {
  display: flex;
  justify-content: flex-end;
  height: 8vh;
  color: #e0a06b;
}

.Socials li {
  margin-right: 5vw;
  margin-top: 1vh;
}

.navBar img {
  height: 4vh;
  margin-top: 1vh;
}

.PAC {
  display: flex;
  flex-direction: row;
  height: 6vh;
  color: var(--nav-blue);
  margin-right: 10vw;
}

.Resume {
  color: orange;
}

ul {
  list-style-type: none;
}
