:root {
  --nav-blue: #4cabe2;
  --nav-bg: #0c0c0c;
  --name-purple: #a161d4;
  --nav-bg-lighter: #181818;
  --text-white: #ccc9c9;
  --subtitle-yellow: #ecdb3a;
}

.projectsDiv {
  height: 100vh;
  background-color: var(--nav-bg-lighter);
}

#projectsHeader {
  font-size: 10vh;
  color: var(--nav-blue);
}

.titleDiv {
  position: relative;
  text-align: left;
  padding-left: 10vh;
}

#DLoJTile {
  height: 50vh;
  padding-left: 10vw;
}

#projectInfoDiv {
  float: left;
  width: 40vw;
  border: solid white 1px;
  height: 50vh;
  color: var(--text-white);
  padding-left: 2vw;
  padding-right: 2vw;
  padding-top: 2vh;
}

#projectPhotoDiv {
  width: 40vw;
  float: left;
  height: 50vh;
}

#projectDescription {
  margin-bottom: 4vh;
}

li img {
  height: 10vh;
}

#projectTitle {
  color: var(--subtitle-yellow);
}

#builtWithLabel {
  color: var(--subtitle-yellow);
}

#builtWithList {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
